/**
* Modal Box
*/
.relative,
.vue-modal .vue-modal-content {
  position: relative;
}
.absolute,
.vue-modal .mask {
  position: absolute;
}
.fixed,
.vue-modal {
  position: fixed;
}
.full-wrapper,
#modals-wrapper,
.vue-modal,
.vue-modal .mask {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.vue-modal {
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
