}
.vue-modal .mask {
  -webkit-backface-visibility: hidden;
  background: rgba(55, 58, 71, 0.9);
  z-index: 999;
  transition: opacity 0.3s;
}
.vue-modal .vue-modal-content {
  background: white;
  width: 50%;
  max-width: 560px;
  min-width: 290px;
  border-radius: 4px;
  padding: 15px;
  z-index: 1000;
  pointer-events: auto;
}
.vue-modal .vue-modal-content h5 {
  margin: 0 0 10px;
}
.vue-modal .vue-modal-content .form,
.vue-modal .vue-modal-content .btn-handler {
  margin: 0;
}
.vue-modal.file-manager-modal .vue-modal-content {
  width: 85%;
  height: 90vh;
  max-width: none;
  overflow-y: scroll;
}
.vue-modal.modal-enter-active .mask {
  opacity: 1;
  pointer-events: none;
}
.vue-modal.modal-enter-active .vue-modal-content {
  animation-duration: 0.3s;
  animation-name: anim-open;
}
@keyframes anim-open {
  0% {
    transform: translate3d(0, calc(100vh), 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes anim-close {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(0, calc(100vh), 0);
  }
}
.modal-enter-active,
.modal-leave-active {
  transition: opacity 0.5s;
}
.modal-enter,
.modal-leave-active {
  opacity: 0;
}
#app.active,
#global.active {
  transform: scale3d(0.95, 0.95, 1);
  pointer-events: none;
}
